'use client'

import { Alert, AlertDescription } from '@/components/ui/alert'
import { Button } from '@/components/ui/button'
import { Card, CardContent } from '@/components/ui/card'
import {
	Form,
	FormControl,
	FormField,
	FormItem,
	FormLabel,
	FormMessage,
} from '@/components/ui/form'
import { Input } from '@/components/ui/input'
import { Textarea } from '@/components/ui/textarea'
import { zodResolver } from '@zod/zod'
import { Clock, Mail, MapPin, Phone, SendIcon } from 'lucide-react'
import { useState } from 'react'
import { useForm } from 'react-hook-form'
import * as z from 'zod'

// Form validation schema
const formSchema = z.object({
	name: z.string().min(2, { message: 'Name must be at least 2 characters.' }),
	email: z.string().email({ message: 'Please enter a valid email address.' }),
	subject: z
		.string()
		.min(5, { message: 'Subject must be at least 5 characters.' }),
	message: z
		.string()
		.min(10, { message: 'Message must be at least 10 characters.' }),
})

type FormValues = z.infer<typeof formSchema>

export default function ContactPage() {
	const [submitted, setSubmitted] = useState(false)

	const form = useForm<FormValues>({
		resolver: zodResolver(formSchema),
		defaultValues: {
			name: '',
			email: '',
			subject: '',
			message: '',
		},
	})

	function onSubmit(data: FormValues) {
		console.log(data)
		// Here you would typically send the data to your API
		setSubmitted(true)
		setTimeout(() => setSubmitted(false), 5000)
		form.reset()
	}

	return (
		<div className='container mx-auto py-12 px-4 md:px-6'>
			<h1 className='text-4xl font-bold text-center mb-10'>Contact Us</h1>

			<div className='grid grid-cols-1 md:grid-cols-2 gap-8'>
				{/* Left Column - Information */}
				<div className='flex flex-col justify-center'>
					<Card className='shadow-md bg-white dark:bg-slate-900 border-0'>
						<CardContent className='pt-6'>
							<h2 className='text-2xl font-semibold mb-6'>
								We'd Love to Hear From You
							</h2>

							<p className='text-muted-foreground mb-8'>
								Have questions about our books, ordering process, or need
								recommendations? Our passionate team of bookworms is here to
								help!
							</p>

							<div className='space-y-6'>
								<div className='flex items-start space-x-4'>
									<MapPin className='h-5 w-5 text-primary mt-1' />
									<div>
										<h3 className='font-medium'>Visit Our Store</h3>
										<p className='text-muted-foreground'>
											123 Bookish Avenue
											<br />
											Literary District
											<br />
											Storyville, ST 12345
										</p>
									</div>
								</div>

								<div className='flex items-start space-x-4'>
									<Clock className='h-5 w-5 text-primary mt-1' />
									<div>
										<h3 className='font-medium'>Store Hours</h3>
										<p className='text-muted-foreground'>
											Monday - Friday: 9am - 8pm
											<br />
											Saturday: 10am - 6pm
											<br />
											Sunday: 12pm - 5pm
										</p>
									</div>
								</div>

								<div className='flex items-start space-x-4'>
									<Phone className='h-5 w-5 text-primary mt-1' />
									<div>
										<h3 className='font-medium'>Call Us</h3>
										<p className='text-muted-foreground'>(555) 123-4567</p>
									</div>
								</div>

								<div className='flex items-start space-x-4'>
									<Mail className='h-5 w-5 text-primary mt-1' />
									<div>
										<h3 className='font-medium'>Email Us</h3>
										<p className='text-muted-foreground'>
											hello@yourbookstore.com
										</p>
									</div>
								</div>
							</div>
						</CardContent>
					</Card>
				</div>

				{/* Right Column - Contact Form */}
				<div>
					<Card className='shadow-md bg-white dark:bg-slate-900 border-0'>
						<CardContent className='pt-6'>
							<h2 className='text-2xl font-semibold mb-6'>Send Us a Message</h2>

							{submitted && (
								<Alert className='mb-6 bg-green-50 text-green-800 border-green-200'>
									<AlertDescription>
										Thank you for your message! We'll get back to you soon.
									</AlertDescription>
								</Alert>
							)}

							<Form {...form}>
								<form
									onSubmit={form.handleSubmit(onSubmit)}
									className='space-y-6'
								>
									<FormField
										control={form.control}
										name='name'
										render={({ field }) => (
											<FormItem>
												<FormLabel>Name</FormLabel>
												<FormControl>
													<Input placeholder='Your name' {...field} />
												</FormControl>
												<FormMessage />
											</FormItem>
										)}
									/>

									<FormField
										control={form.control}
										name='email'
										render={({ field }) => (
											<FormItem>
												<FormLabel>Email</FormLabel>
												<FormControl>
													<Input
														placeholder='your.email@example.com'
														{...field}
													/>
												</FormControl>
												<FormMessage />
											</FormItem>
										)}
									/>

									<FormField
										control={form.control}
										name='subject'
										render={({ field }) => (
											<FormItem>
												<FormLabel>Subject</FormLabel>
												<FormControl>
													<Input placeholder="What's this about?" {...field} />
												</FormControl>
												<FormMessage />
											</FormItem>
										)}
									/>

									<FormField
										control={form.control}
										name='message'
										render={({ field }) => (
											<FormItem>
												<FormLabel>Message</FormLabel>
												<FormControl>
													<Textarea
														placeholder='Tell us how we can help you...'
														className='min-h-32'
														{...field}
													/>
												</FormControl>
												<FormMessage />
											</FormItem>
										)}
									/>

									<Button type='submit' className='w-full'>
										<SendIcon className='mr-2 h-4 w-4' /> Send Message
									</Button>
								</form>
							</Form>
						</CardContent>
					</Card>
				</div>
			</div>
		</div>
	)
}
